import React, { useState } from "react";


const Contact =()=>{
    const[Data,setData]=useState({
        FullName:"",
        phone:"",
        email:"",
        msg:"",
    });
    const InputEvent=(event)=>{
        const {name,value}=event.target;
        setData((preval)=>{
            return{
                ...preval,
                [name]:value,
            };
        });
    }
  const formsubmit=(e)=>{
      e.preventDefault();
      alert(
          `My Name is ${Data.FullName}.My mobile number is ${Data.phone}. My email addres is ${Data.email}`
      )
  }  
    return (
     <>
     <div className="my-5">   
    <h1 className="text-center">Contact Us</h1>
    </div>
    <div className="container contact_div">
        <div className="row">
            <div className="col-md-6 col-10 mx-auto">
                <form onSubmit={formsubmit}>
                <div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Full Name</label>
  <input type="text" class="form-control" id="exampleFormControlInput1" name="FullName" value={Data.FullName} onChange={InputEvent} placeholder="Enter your name"/>
</div>
<div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Email address</label>
  <input type="email" class="form-control" id="exampleFormControlInput1" name="email" value={Data.email} onChange={InputEvent} placeholder="name@example.com"/>
</div>
<div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Phone number</label>
  <input type="number" class="form-control" id="exampleFormControlInput1" name="phone" value={Data.number} onChange={InputEvent} placeholder="Enter your phone number"/>
</div>

<div class="mb-3">
  <label for="exampleFormControlTextarea1" class="form-label">Describe yourself</label>
  <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" name="msg" value={Data.msg} onChange={InputEvent}></textarea>
</div>
 <button type="submit" class="btn btn-primary">Submit</button>
                </form>
            </div>
        </div>
    </div>
   </>
    )   
};
export default Contact;
